import { useState, useEffect } from "react";
import styles from "@/styles/Countdown.module.css";

export default function Countdown() {
  const [timeLeft, setTimeLeft] = useState({});
  const [isNewYear, setIsNewYear] = useState(false);

  // ‚≠ê Yahan target date change kar sakte ho easily
  const targetDate = new Date("December 31, 2025 23:59:59").getTime();
  // Example: Dusre saal ke liye ‚Üí new Date("December 31, 2026 23:59:59").getTime();

  useEffect(() => {
    const calculateTimeLeft = () => {
      const now = new Date().getTime();
      const difference = targetDate - now;

      if (difference <= 0) {
        setIsNewYear(true);
        setTimeLeft({});
        return;
      }

      const days = Math.floor(difference / (1000 * 60 * 60 * 24));
      const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((difference % (1000 * 60)) / 1000);

      setTimeLeft({ days, hours, minutes, seconds });
      setIsNewYear(false);
    };

    // Pehli baar immediately calculate karo
    calculateTimeLeft();

    // Har second update karo
    const timer = setInterval(calculateTimeLeft, 1000);

    // Cleanup on unmount
    return () => clearInterval(timer);
  }, [targetDate]); // targetDate change hone par dobara effect chalega

  return (
    <section className={`countdown ${styles.countdown}`}>
      <div className={`bgImg ${styles.bgImg}`}>
        <video
          className={`imacVideo ${styles.imacVideo}`}
          autoPlay
          loop
          muted
          playsInline
          poster="/images/new2026.mp4" // optional fallback image
        >
          <source src="/images/new2026.mp4" type="video/mp4" />
        </video>
      </div>

      <div className="container">
        <div className={`intro max-w-[1000px] m-auto ${styles.intro}`}>
          <h2>‚è≥ Countdown to Midnight
            Welcome 2026 ‚ú®</h2>
        </div>

        <div className={`countdownWrapper sm:max-w-auto sm:m-auto flex-wrap ${styles.countdownWrapper}`} id="countdownWrapper">
          {isNewYear ? (
            <p className={styles.newYearText}>üéâ Happy New Year 2026! üéâ</p>
          ) : (
            <>
              <p>
                {timeLeft.days !== undefined ? timeLeft.days : "--"}{" "}
                <span>Days</span>
              </p>
              <p>
                {timeLeft.hours !== undefined ? String(timeLeft.hours).padStart(2, "0") : "--"}{" "}
                <span>Hours</span>
              </p>
              <p>
                {timeLeft.minutes !== undefined ? String(timeLeft.minutes).padStart(2, "0") : "--"}{" "}
                <span>Minutes</span>
              </p>
              <p>
                {timeLeft.seconds !== undefined ? String(timeLeft.seconds).padStart(2, "0") : "--"}{" "}
                <span>Seconds</span>
              </p>
            </>
          )}
        </div>
      </div>
    </section>
  );
}